import HeaderBar from './Header-bar'
import clipImage from './clipImage'
import { MessageBox, Toast } from 'mint-ui'
import axios from 'axios'
import QS from 'qs'
import { ComUtils } from 'common/utils'
const newslist = () => import(/* webpackChunkName: 'ImportFuncDemo' */ 'pages/home/newslist.vue') //新闻详情页面
const pictwite = () => import(/* webpackChunkName: 'ImportFuncDemo' */ 'pages/home/pictwite.vue') //新闻详情页面
import Photoclipcom from "./photoclip.vue";
const HomeCom = () => import(/* webpackChunkName: 'ImportFuncDemo' */ './homecom.vue') //新闻详情页面
const HomeCom1 = () => import(/* webpackChunkName: 'ImportFuncDemo' */ './homecom1.vue') //新闻详情页面
const listcom = () => import(/* webpackChunkName: 'ImportFuncDemo' */ './listcom.vue') //新闻详情页面


const allglobalcomponents = {
    install: function (Vue) {
        Vue.component('HeaderBar', HeaderBar)
        Vue.component('clipImage', clipImage)
        Vue.component('newslist', newslist)
        Vue.component('Photoclipcom', Photoclipcom)
        Vue.component('HomeCom', HomeCom1)
        Vue.component('Listcom', listcom)
        Vue.component('pictwite', pictwite)

        Object.defineProperties(Vue.prototype, {
            $message: {
                get() {
                    return MessageBox
                },
                enumerable: false, // 不可枚举
                configurable: false // 不可重写
            },
            $toast: {
                get() {
                    return Toast
                },
                enumerable: false, // 不可枚举
                configurable: false // 不可重写
            },
            $ajax: {
                get() {
                    return axios
                },
                enumerable: false, // 不可枚举
                configurable: false // 不可重写
            },
            $qs: {
                get() {
                    return QS
                },
                enumerable: false, // 不可枚举
                configurable: false // 不可重写
            },
            $getOtherUser: {
                get() {
                    return ComUtils.getParams();
                },
                enumerable: false, // 不可枚举
                configurable: false // 不可重写
            },
            $getUser: {
                get() {
                    return ComUtils.getLoginParams();
                },
                enumerable: false, // 不可枚举
                configurable: false // 不可重写
            },
            $getSetUser: {
                get() {
                    return ComUtils.getSetUserParams();
                },
                enumerable: false, // 不可枚举
                configurable: false // 不可重写
            },
            $isArrayfn: {
                get() {
                    return ComUtils.isArrayfn;//判断是否为数组
                },
                enumerable: false, // 不可枚举
                configurable: false // 不可重写
            }
        })
    }
}

export default allglobalcomponents


