import * as types from 'common/mutation-types'
import yxMutations from './mutations/yxStore';

const mutations = {
    [types.BOTTOM_STATE](state, bottomnavstate) {
        state.bottomnavstate = bottomnavstate;
    },
    [types.HOMEDETAILID_STATE](state, homedetailid) {
        state.homedetailid = homedetailid;
    },
    [types.GETUSERDATA](state, userinfomation) {
        state.userinfomation = userinfomation;
    },
    [types.PULLBUTTOMSELECT](state, buttomSelected) {
        state.buttomSelected = buttomSelected;
    },
    [types.PULLMESPOT](state, meSpot) {
        state.meSpot = meSpot;
    },
    ...yxMutations
}

export default mutations;



