import api from '../index'
import urls from './urls'

// import { formatUrl } from '../../common/utils'
// 这个东西是unsplash要求加在header里的验证，有兴趣的同学可以去看他的api，此处不再赘述
const header = {
  // 'Authorization': 'Client-ID xxxxxxx'
}

export default {
  GETfn(params, urlKey) {
    return api.get(urls[urlKey], params, header)
  },
  POSTfn(params, urlKey) {
    return api.post(urls[urlKey], params, header)
  }
}


