<template>
  <mt-header class="c-color" :title="titleName">
    <mt-button icon="back" @click.native="gobackfn" slot="left" v-show="needback"></mt-button>
    <mt-button icon="back" @click.native="backcustomfn" slot="left" v-show="needcustomback"></mt-button>
    <mt-button icon="more" slot="right" v-show="needmore" @click="morefn"></mt-button>
    <mt-button slot="right" v-show="needshare">分享</mt-button>
    <mt-button slot="right" v-show="needwrite" @click.native="writesubmit">
      <span class="addicon iconfont icon-add" slot="icon"></span>
    </mt-button>
    <mt-button slot="right" v-show="needsubmit" @click.native="childsubmit">提交</mt-button>
    <mt-button slot="right" v-show="needcreate" @click.native="createfn">
      <span class="addicon iconfont icon-add" slot="icon"></span>
    </mt-button>
    <mt-button slot="right" v-show="needchange" @click.native="changefn">更改</mt-button>
    <mt-button slot="right" v-show="needjoin" @click.native="joinfn">
      <slot></slot>
    </mt-button>

    <slot name="search"></slot>
  </mt-header>
</template>

<script>
export default {
  props: [
    "tit",
    "needm",
    "needb",
    "needf",
    "needw",
    "needs",
    "needc",
    "needch",
    "needcsb",
    "needj"
  ],
  data() {
    return {
      titleName: "",
      needmore: false,
      needback: false,
      needshare: false,
      needwrite: false,
      needcreate: false,
      needchange: false,
      needcustomback: false,
      needjoin: false,
      UserInfo: this.$getUser
    };
  },
  computed: {},
  created() {
    this.titleName = this.tit;
    this.needmore = this.needm;
    this.needback = this.needb;
    this.needshare = this.needf;
    this.needwrite = this.needw;
    this.needsubmit = this.needs;
    this.needcreate = this.needc;
    this.needchange = this.needch;
    this.needcustomback = this.needcsb;
    this.needjoin = this.needj;
  },
  mounted() {},
  watch: {
    tit: {
      handler(newv, oldv) {
        this.titleName = newv;
      },
      deep: true
    }
  },
  methods: {
    writesubmit() {
      this.$emit("fatherwrite");
    },
    childsubmit() {
      this.$emit("fathersubmit");
    },
    createfn() {
      this.$emit("fathercreate");
    },
    morefn() {
      this.$emit("fathermorefn");
    },
    changefn() {
      this.$emit("fatherchange");
    },
    backcustomfn() {
      this.$emit("fatherbackcall");
    },
    gobackfn() {
      if (window.hasOwnProperty("webkit")) {
      
        window.webkit.messageHandlers.closeWebView.postMessage();
      }else{
        this.$router.go(-1); //返回上一页
      }
      
    },
    joinfn() {
      this.$emit("fatherjoin");
    }
  },
  components: {}
};
</script>

<style scoped>
.mint-header {
  width: 100% !important;
  height: 44px;
  line-height: 44px;
  font-size: 16px;
  z-index: 99999;
}

.addicon {
  color: #3966a7 !important;
  font-size: 16px;
}

</style>



