<template>
  <mt-tabbar v-model="selectedfn" v-show="needbottom" fixed>
    <mt-tab-item id="homePage" @click.native="toPath('homePage','C')" class="homePage" >
       <img slot="icon" :src="require('images/iwxuanzhong.png')"  v-show="show_img">
        <img slot="icon" :src="require('images/ixuanzhong.png')" v-show="img_show">首页
      <!-- <span class="spot homtspot">&nbsp;</span> -->
    </mt-tab-item>
    <mt-tab-item id="news" @click.native="toPath('news','R')" class="news">
       <img slot="icon" :src="require('images/xwxuanzhong.png')" v-show="show_img1">
        <img slot="icon" :src="require('images/xxuanzhong.png')" v-show="img_show1">
      <span class="newsspot" v-show="newsspot">&nbsp;</span>
      消息
      <span class="spot newsspot" v-show="newsspot">&nbsp;</span>
    </mt-tab-item>
    <mt-tab-item id="community" @click.native="toPath('community','D')" class="community">
     <img slot="icon" :src="require('images/gwxuanzhong.png')" v-show="show_img2">
        <img slot="icon" :src="require('images/gxuanzhong.png')" v-show="img_show2">
      <span class="communspot" v-show="communspot">&nbsp;</span>
      社群
      <span class="spot communspot" v-show="communspot">&nbsp;</span>
    </mt-tab-item>
    <mt-tab-item id="find" @click.native="toPath('find','S')" class="find">
     <img slot="icon" :src="require('images/fwxuanzhong.png')" v-show="show_img3">
        <img slot="icon" :src="require('images/fxuanzhong.png')" v-show="img_show3">
      <span class="findspot" v-show="findspot">&nbsp;</span>
      发现
      <span class="spot findspot" v-show="findspot">&nbsp;</span>
    </mt-tab-item>
    <mt-tab-item id="me" @click.native="toPath('me','W')" class="me">
    <img slot="icon" :src="require('images/wwxuanzhong.png')" v-show="show_img4">
        <img slot="icon" :src="require('images/wxuanzhong.png')" v-show="img_show4">
      <span class="mespot" v-show="mespot">&nbsp;</span>
      我的
      <span class="spot mespot" v-show="mespot">&nbsp;</span>
    </mt-tab-item>
  </mt-tabbar>
</template>

<script>
import { ComUtils } from "common/utils";

export default {
  name: "page-tabbar",
  data() {
    return {
      selectedfn:"homePage",
      UserInfo: this.initParam(),
      findspot: false,
      communspot: false,
      newsspot: false,
      show_img: false,
      img_show: true,
      show_img1: true,
      img_show1: false,
      show_img2: true,
      img_show2: false,
      show_img3: true,
      img_show3: false,
       show_img4: true,
      img_show4: false,
    };
  },
  methods: {
    initParam() {
      let results = ComUtils.getLoginParams();
      if (results != "" && results != null && results != undefined) {
        if (results.id == "" || results.id == null || results.id == undefined) {
          this.UserInfo = this.$getUser;
        }
      }
    },
    async notmessages() {
      try {
        let params = {};
        let res = await this.$api.all.POSTfn(params, "notice_have");
        if (res.result) {
          if (res.result == "ok") {
            this.$store.dispatch("pullmespot", true);
            this.$forceUpdate();
          } else {
            this.$store.dispatch("pullmespot", false);
            this.$forceUpdate();
          }
        }
      } catch (e) {
        console.log(e);
      }
    },
    
    toPath(id,state) {
      
      this.$router.push({
        path: id
      });
      
    }
  },
  created() {
    this.notmessages();
  },
  mouted() {
    //将要给原生调用的方法挂载到 window 上面
    // window.callJsFunction = this.callJsFunction;
    this.notmessages();
  },
  computed: {
    needbottom() {
      return this.$store.state.bottomnavstate;
    },
   selectedfn1 : {
      get() {
        return this.$store.state.buttomSelected;
      },
      set() {}
    },
    mespot() {
      return this.$store.state.meSpot;
    }
  },
  watch: {
    selectedfn(val, old) {
      switch (val) {
        case "homePage":
          this.show_img = false;
          this.show_img1 = true;
          this.show_img2 = true;
          this.show_img3 = true;
           this.show_img4 = true;
          this.img_show = true;
          this.img_show1 = false;
          this.img_show2 = false;
          this.img_show3 = false;
           this.img_show4 = false;
          break;
        case "news":
          this.show_img = true;
          this.show_img1 = false;
          this.show_img2 = true;
          this.show_img3 = true;
           this.show_img4 = true;

          this.img_show = false;
          this.img_show1 = true;
          this.img_show2 = false;
          this.img_show3 = false;
           this.img_show4 = false;
          break;
        case "community":
          this.show_img = true;
          this.show_img1 = true;
          this.show_img2 = false;
          this.show_img3 = true;
           this.show_img4 = true;

          this.img_show = false;
          this.img_show1 = false;
          this.img_show2 = true;
          this.img_show3 = false;
          this.img_show4 = false;
          break;
        case "find":
          this.show_img = true;
          this.show_img1 = true;
          this.show_img2 = true;
          this.show_img3 = false;
          this.show_img4 = true;
          this.img_show = false;
          this.img_show1 = false;
          this.img_show2 = false;
          this.img_show3 = true;
          this.img_show4 = false;
          break;
           case "me":
          this.show_img = true;
          this.show_img1 = true;
          this.show_img2 = true;
          this.show_img3 = true;
          this.show_img4 = false;

          this.img_show = false;
          this.img_show1 = false;
          this.img_show2 = false;
          this.img_show3 = false;
          this.img_show4 =true ;
          break;
        default:
          break;
      }
    },

    $route(to, from) {
      this.initParam();
      this.notmessages();
    }
  }
};
</script>

<style lang="css" scoped>
.mint-tabbar {
  background: #fff;
  z-index: 99999;
  border-top: 1px solid #eee;
  max-height: 50px !important;
}

>>> .mint-tab-item {
  color: #222;
}

span.iconfont {
  display: block;
  width: 24px;
  height: 24px;
  margin-right: 5px;
}
.mint-tabbar img{
  height: 25px;
}
.mint-tabbar > .mint-tab-item.is-selected {
  background: #fff;
  color: #3966a7;
}

.mint-tabbar > .mint-tab-item.is-selected .iconfont1 {
  background: #fff;
  color: #3966a7 !important;
}

.mint-tabbar.is-fixed {
  position: relative !important;
  width: 100% !important;
}

.mint-tab-item-label {
  font-size: 12px;
}
.mint-tab-item >>> .mint-tab-item-icon {
  width: 24px !important;
  height: 24px !important;
  margin: 0 auto 10px !important;
}

.spot {
  display: inline-block;
  width: 6px;
  height: 6px;
  background: rgb(216, 28, 28);
  border-radius: 50%;
}
</style>



