<template>
  <div class="photocover">
    <input type="file" name="imagefile" id="imagefile" ref="imagefile" />
    <div id="photoclip" v-show="photoflag" ref="imagicon">
      <HeaderBar :needcsb="needb" :tit="titleName" @fatherbackcall="hideThisfn"></HeaderBar>
      <div class="k"></div>
      <button id="clipBtn" ref="clipBtn">上传</button>
      <canvas id="myCanvas"></canvas>
    </div>
  </div>
</template>

<script>
import PhotoClip from "photoclip";

export default {
  name: "photoclip",
  props: ["outputSizeWidth", "outputSizeHeight", "inSize"],
  data() {
    return {
      photoflag: false,
      options: {},
      formData: new FormData(),
      outputsizeWidth: 0,
      outputsizeHeight: 0,
      insize: [],
      needb: true,
      titleName: "图片裁剪"
    };
  },
  computed: {},
  created() {
    this.outputsizeWidth = parseInt(this.outputSizeWidth);
    this.outputsizeHeight = parseInt(this.outputSizeHeight);

    this.insize = this.inSize;
    console.log(this.insize);
  },
  mounted() {
    this.initClipFn();
  },
  watch: {},
  methods: {
    initClipFn() {
      // debugger;
      let that = this;
      this.options = {
        size: that.insize,
        outputSize: [that.outputsizeWidth, that.outputsizeHeight],
        file: "#file",
        view: "#" + that.viewdoms,
        ok: "#clipBtn",
        loadStart: function() {
          //   console.log("开始读取照片");
        },
        loadComplete: function() {
          //   console.log("照片读取完成");
        },
        done: function(dataURL) {
          that.$emit("fatherimgcall", dataURL);
          console.log(dataURL);
          let img = new Image();
          img.src = dataURL;
          img.onload = function() {
            let canvas = document.getElementById("myCanvas");
            let context = canvas.getContext("2d");
            context.drawImage(img, 0, 0);

            canvas.toBlob(function(blob) {
              that.formData.append("logo", blob);
              that.formData.append("file", blob);
              that.$emit("fatherfromcall", that.formData);
            });
          };
        },
        fail: function(msg) {
          that.$toast(msg);
        }
      };

      let pc = new PhotoClip(this.$refs.imagicon, this.options);
      this.$refs.imagefile.addEventListener("click", () => {
        this.photoflag = !this.photoflag;
      });
      this.$refs.clipBtn.addEventListener("click", () => {
        this.photoflag = !this.photoflag;
      });
      this.$refs.imagefile.addEventListener("change", function() {
        pc.load(this.files[0]);
      });
    },
    hideThisfn() {
      this.photoflag = !this.photoflag;
    }
  },
  components: {},
  destroyed() {
    this.$destory();
  }
};
</script>

<style  lang="css">
/* .photocover {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 99999999999999;
} */

#imagefile {
  width: 100%;
  height: 100%;
  background: green;
  display: inline-block;
  position: absolute;
  z-index: 99999999;
}

#myCanvas {
  opacity: 0;
  width: 300px;
  height: 150px;
  z-index: 9999999999999;
}
</style>



