/*********
  1PX 使用示范
  在赋予一个dom元素像素为1px时候，在后面添加：
  background ： white svg(square param(--color "自己定义的颜色，一般为透明色，不显示")
**********/

@svg square {
  @rect {
    fill: var(--color, black);
    width: 100%;
    height: 100%;
  }
}

