  outline: none;
  background: transparent;
}
input[type="file"] {
  outline: none;
  background: transparent;
  opacity: 0;
}
input[type="file"]:focus {
  outline: none;
  background: transparent;
  opacity: 0;
}

::selection {
  background: transparent;
}
::-moz-selection {
  background: transparent;
}

img {
  content: normal !important;
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  position: relative;
}

* {
  font-family: -apple-system, "PingFang SC", "Helvetica Neue", Helvetica, STHeiTi, sans-serif;
}

#app {
  font-family: -apple-system, "PingFang SC", "Helvetica Neue", Helvetica, STHeiTi, sans-serif;
