} */

#photoclip {
  background: #eee;
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  right: 0;
  margin: auto;
  bottom: 0;
  top: 0;
  z-index: 99999;
}

#clipBtn {
  width: 120px;
  height: 36px;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  bottom: 18%;
  color: #fff;
  background: #4a8dff;
  border-radius: 5px;
  z-index: 99999;
}
