import api from '../index'
import urls from './urls'
import { ComUtils } from 'common/utils'


// import { formatUrl } from '../../common/utils'
// 这个东西是unsplash要求加在header里的验证，有兴趣的同学可以去看他的api，此处不再赘述
const header = {
  // 'Authorization': 'Client-ID xxxxxxx'
}

export default {
  GETfn(params, urlKey, keyid) {                                                                                                
    const _params = ComUtils.getLoginParams();

    let url;
    if (keyid) {
      url = urls[urlKey] + ("/" + keyid)
    }
    else {
      url = urls[urlKey]
    }
    if (_params != "" && _params != null) {
      if (params.userid == "" || params.userid == null || params.userid == undefined) {
        params.userid = _params.id;
      }
      // if (params.user_id == "" || params.user_id == null || params.user_id == undefined) {
      //   params.user_id = _params.id;
      // }
      // if (params.token == "" || params.token == null || params.token == undefined) {
      //   params.token = _params.token;
      // }
      // if (params._token == "" || params._token == null || params._token == undefined) {
      //   params.token = _params._token;
      // }
    }
    return api.get(url, params, header)
  },
  POSTfn(params, urlKey) {
    const _params = ComUtils.getLoginParams();

    if (Object.prototype.toString.call(params) == '[object FormData]') {
      if (params.userid == "" || params.userid == null || params.userid == undefined) {
        params.append("userid", _params.id);
      }
      // if (params.user_id == "" || params.user_id == null || params.user_id == undefined) {
      //   params.append("user_id", _params.id);

      // }
      // if (params.token == "" || params.token == null || params.token == undefined) {
      //   params.append("token", _params.token);

      // }
      // if (params._token == "" || params._token == null || params._token == undefined) {
      //   params.append("_token", _params._token);
      // }
    }
    else if (_params != "" && _params != null) {
      if (params.userid == "" || params.userid == null || params.userid == undefined) {
        params.userid = _params.id;
      }
      // if (params.user_id == "" || params.user_id == null || params.user_id == undefined) {
      //   params.user_id = _params.id;
      // }
      // if (params.token == "" || params.token == null || params.token == undefined) {
      //   params.token = _params.token;
      // }
      // if (params._token == "" || params._token == null || params._token == undefined) {
      //   params.token = _params._token;
      // }
    }
    return api.post(urls[urlKey], params, header)
  }
}


