import store from '../store/index.js'

const createStyle = function (str) {
    let Node = document.createElement("style");
    Node.type = "text/css";
    if (Node.styleSheet) {
        Node.styleSheet.cssText = str;
    } else {
        Node.innerHTML = str;
    }
    document.getElementsByTagName("head")[0].appendChild(Node);
}

