const saveParams = function (data) {
    window.localStorage.setItem("userInformation", JSON.stringify(data));
    window.localStorage.setItem("isLogin", true);
    store.dispatch("pullUserinfo", data);
}

const isArrayfn = function (o) {
    return Object.prototype.toString.call(o) == '[object Array]';
}

const skipPage = function (item, that) {
    // 跳转到详情页
    debugger;
    if (item.type_id == "2") {
        localStorage.setItem("homedata", JSON.stringify(item));
        that.$router.push({
            path: "/pictureDetail",
            name: "pictureDetail"
        });
    } else if (item.type_id == "13") {
        localStorage.setItem("homedata", JSON.stringify(item));
        that.$router.push({
            path: "/vadiodetail",
            name: "vadiodetail"
        });
    } else if (item.type_id == "4") {
        localStorage.setItem("zhuantidata", JSON.stringify(item));
        that.$router.push({
            path: "/subjectDetail"
        });
    } else {
        localStorage.setItem("homedata", JSON.stringify(item));
        // console.log(item);
        localStorage.setItem("atype", item.category_id);
        that.$router.push({
            path: "/homeDetail",
            name: "homeDetail",
            params: {
                data: item
            }
        });
    }


}



export const ComUtils = {
    createStyle,
    getParams,
    getLoginParams,
    getSetUserParams,
    saveParams,
    isArrayfn,
    skipPage
}



