import axios from 'axios'
import browser from './browser.js'
import { MessageBox, Indicator } from 'mint-ui';
// import qs from 'qs'
// 创建 axios 实例
let http = axios.create({
  // headers: {'Content-Type': 'application/json'},
  timeout: 35000
})

// 设置 post、put 默认 Content-Type
http.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'
http.defaults.headers.get['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'
http.defaults.headers.put['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'

// 添加请求拦截器
http.interceptors.request.use(config => {
  if (config.method === 'post' || config.method === 'put') {
    // post、put 提交时，将对象转换为string, 为处理Java后台解析问题
    // config.data = JSON.stringify(config.data)
    // config.data = qs.stringify(config.data)
  } else if (config.method === 'get' && browser.isIE) {
    // 给GET 请求后追加时间戳， 解决IE GET 请求缓存问题
    let symbol = config.url.indexOf('?') >= 0 ? '&' : '?'
    config.url += symbol + '_=' + Date.now()
  }
  // 请求发送前进行处理
  // Indicator.open({
  //   //text: '加载中,请稍后...',
  //   spinnerType: 'fading-circle'
  // });

  // setTimeout(() => {
  //   Indicator.close();
  // }, 3000)

  return config
}, error => {
  MessageBox.alert('数据加载超时,请检查您的网络或稍后重试!').then(action => {
    Indicator.close();
  });
  // 请求错误处理
  return Promise.reject(error)
})

// 添加响应拦截器
http.interceptors.response.use(response => {
  // Indicator.close();
  let { data } = response;
  if(data.result==500){
    app.loginOut()
  }
  // console.log(data)
  return data
}, error => {
  let info = {}
  let { status, statusText, data } = error.response
  if (!error.response) {
    info = {
      code: 5000,
      msg: 'Network Error'
    }
  } else {
    // 此处整理错误信息格式
    info = {
      code: status,
      data: data,
      msg: statusText
    }
  }

  MessageBox.alert('数据加载失败!').then(action => {
    Indicator.close();
  });
  return Promise.reject(info)
})

/**
 * 创建统一封装过的 axios 实例
 * @return {AxiosInstance}
 */
export default function () {
  return http
}


