import * as types from 'common/mutation-types'

const actions = {
    pullbottomnavstate({ commit }, needbu) {
        commit(types.BOTTOM_STATE, needbu)
    },
    getDatilhomeId({ commit }, id) {
        commit(types.HOMEDETAILID_STATE, id)
    },
    pullUserinfo({ commit }, userinfo) {
        commit(types.GETUSERDATA, userinfo)
    },
    pullbuttomselected({ commit }, selectd) {
        commit(types.PULLBUTTOMSELECT, selectd) //PULLMESPOT
    },
    pullmespot({ commit }, meSpot) {
        commit(types.PULLMESPOT, meSpot) //PULLMESPOT
    }
}

export default actions


